export const makePeerComparison = (list: number[]) => {

  if (list.length === 0) list = [0,1];

  const minimum = Math.min(...list);
  const maximum = Math.max(...list);
  const average = Math.round((list.reduce((a: number, b: number) => a + b) / list.length) * 10) / 10;

  return { minimum, maximum, average };
}