import React, { PureComponent } from "react";
import {Line} from 'react-chartjs-2';


export default class GraphView extends PureComponent<{data: any, average: number | string}> {
  render() {
    return (
      <React.Fragment>
        <h2>Your predicted grade is a <b>{ this.props.average }</b>.</h2>
        <Line
          data={this.props.data}
          options={{legend: {
            display: false
          }}}
        />
      </React.Fragment>
    )
  }
}